import React, { useState } from 'react';
import './MembersPage.scss';

const MembersPage = () => {
    const [activeTab, setActiveTab] = useState('id'); //     

    const handleTabClick = (tab) => {
      setActiveTab(tab);
    };

    return ( 
    <main className='MembersPage'>
        <div className='Bg'>
            <div className='UsersMemberWrap'>
                <div className='logo'></div>
                <div className='FoundTab'>
                    <div className={`tab ${activeTab === 'id' ? 'active' : ''}`} onClick={() => handleTabClick('id')}> </div>
                    <div className={`tab ${activeTab === 'password' ? 'active' : ''}`} onClick={() => handleTabClick('password')}> </div>
                </div>
                {activeTab === 'id' ? (
            <div className='IdNumber'>
              <input type='text' placeholder=''></input>
              <input type='text' placeholder=''></input>
              <button> </button>
            </div>
          ) : (
            <div className='PasswordRecovery'>
              <input type='text' placeholder=''></input>
              <input type='text' placeholder=''></input>
              <button> </button>
            </div>
          )}
        </div>
        <div className="MembersPageFooter"></div>
    </div>

</main>
     );
};
 
export default MembersPage;