import React, { useState, useRef, useEffect } from 'react';
import './NoticePage.scss';
import FooterSecond from '../components/layouts/FooterSecond';
import { useParams } from 'react-router-dom';
import List from '../components/layouts/notice/List';
import FAQ from '../components/layouts/notice/FAQ';

const NoticePage = () => {
  const { tab } = useParams();
  useEffect(() => {
    if (!tab) {
      setActiveTab('notice');
    } else if (tab === 'inform') {
      setActiveTab('notice');
    } else if (tab === 'inquiry') {
      setActiveTab('inquiry');
    } else if (tab === 'faq') {
      setActiveTab('faq');
    }
  }, [tab]);

  const [activeTab, setActiveTab] = useState('notice');

  const tabRefs = useRef([]);

  useEffect(() => {
    //       
    const activeTabElement = tabRefs.current[activeTab];
    const square = document.querySelector('.square');
    const left = activeTabElement.offsetLeft;
    square.style.transform = `translateX(${left}px)`;
  }, [activeTab]);

  const handleTabClick = (tab) => {
    setActiveTab(tab);
  };

  const renderTabContent = () => {
    switch (activeTab) {
      case 'notice':
        return <List />;
      case 'inquiry':
        return (
          <div>
            <div className="qna">
              <ul>
                <li>
                  <p className="first"></p>
                  <div>
                    <input
                      type="text"
                      placeholder=" "
                      name="name"
                      id="name"
                    ></input>
                  </div>
                </li>
                <li>
                  <p className="first"></p>
                  <div>
                    <input
                      type="tel"
                      placeholder=" "
                      name="phone"
                      id="phone"
                    ></input>
                  </div>
                </li>
                <li>
                  <p className="first"></p>
                  <div>
                    <input
                      type="email"
                      placeholder=") dpdnjs@naver.com"
                      name="email"
                      id="email"
                    ></input>
                  </div>
                </li>
                <li>
                  <p className="first"></p>
                  <div>
                    <textarea
                      name="content"
                      id="content"
                      cols="30"
                      rows="10"
                      placeholder=" "
                    ></textarea>
                  </div>
                </li>
              </ul>
              <div className="cont">
                <h2> /</h2>
                <div className="contTxt">
                  <p>
                           
                     ,      
                         .
                    <br />
                    <br />
                     SMUF 2023     
                          
                      .
                    <br />
                    <br />
                     SMUF 2023      
                         , 
                         24 
                     .
                    <br />
                    <br />
                    SMUF 2023     
                         
                        
                        .   
                          
                            
                        .
                    <br />
                    <br />
                    7 ( )
                    <br />
                    <br />
                         
                    , 
                          
                    .     ,  
                       .
                    <br />
                    <br />
                      : 02-2100-2499 (www.kopico.go.kr) 
                     : () 118 (privacy.kisa.or.kr) 
                      : 02-3480-3571 (www.spo.go.kr) 
                      : () 182
                    (cyberbureau.police.go.kr)
                    <br />
                    <br />
                    35( ), 36(
                    ), 37(  )  
                            
                            
                        .
                    <br />
                    <br />
                         
                    (www.simpan.go.kr)   .
                    <br />
                    <br />
                    8 (  )
                    <br />
                    <br />
                    SMUF 2023      
                      .
                    <br />
                    <br />
                    9 ()
                    <br />
                    <br />  2023 7 31 ,
                      SMUF 2023     ,  
                          .
                  </p>
                </div>
                <div className="checkWrap">
                  <input className="checkBox" type="checkbox" />
                  <p>  .</p>
                </div>
              </div>
              <div>
                <div className="submitBtn"></div>
              </div>
            </div>
          </div>
        );
      case 'faq':
        return <FAQ />;
      default:
        return null;
    }
  };

  return (
    <main className="NoticePage">
      <div className="NoticePageHeader">
        <h1></h1>
        <p> .</p>
      </div>
      <div className="noticeSection">
        <div className="noticeBtn">
          <div>
            <p
              ref={(el) => (tabRefs.current['notice'] = el)} //  
              className={`tab  ${activeTab === 'notice' ? 'active' : ''}`}
              onClick={() => handleTabClick('notice')}
            >
              
            </p>
            <p
              ref={(el) => (tabRefs.current['inquiry'] = el)}
              className={`tab  ${activeTab === 'inquiry' ? 'active' : ''}`}
              onClick={() => handleTabClick('inquiry')}
            >
              
            </p>
            <p
              ref={(el) => (tabRefs.current['faq'] = el)}
              className={`tab ${activeTab === 'faq' ? 'active' : ''}`}
              onClick={() => handleTabClick('faq')}
            >
              FAQ
            </p>
            {/*    */}
            <div className="square"></div>
          </div>
          {/* <div className="noticeBtnColor"></div> */}
        </div>
        {renderTabContent()}
      </div>
      <FooterSecond />
    </main>
  );
};

export default NoticePage;
